import React, { Component } from 'react';
import Filter from './components/Filter/Filter';
import axios from 'axios';

import './App.css';

class App extends Component {
	constructor(props) {
		super(props);
		this.state = {
			sources: '',
			articles: ''
		}
		const apiSources = 'https://newsapi.org/v1/sources';
		const apiArticles = 'https://newsapi.org/v1/articles';
		const apiKey = 'apiKey=1d4fef7aa6c44d9ea3ab179836a24b9a';
	}

	componentWillMount() {
		// GET sources
		axios.get(this.apiSources + this.apiKey)
			.then(function (response) {
				console.log(response);
			})
			.catch(function (error) {
				console.log(error);
			});
	}

  render() {
    return (
      <div className="App">
				<Filter sources={this.sources} articles={this.articles} />
      </div>
    );
  }
}

export default App;



